<template>
    <div>
        <div ref='myChart' class="chart"></div>
    </div>
</template>
<script>

var echars = require('echarts');
import axios from 'axios';


export default {
    data(){
        return {
            mychart: null,
            dataV1:[],
            dataV2:[], 
            dataV3:[],
            dataV4:[] 
        };
    },
    mounted: function(){
        // 指定图表的配置项和数据
        let option = {
            title: {
                text: '历年温度曲线'
            },
            tooltip: {},
            xAxis: {
                type: 'time',
                boundaryGap: false
            },
            yAxis: {
                type: 'value',
                boundaryGap: [0, 50]
            },
            series: [
                {
                    name: 'Fake Data',
                    type: 'line',
                    smooth: true,
                    symbol: 'none',
                    areaStyle: {},
                    data: [],
                }
            ]
        };
        let that = this;
        this.$nextTick(function () {
            let mychart = echarts.init(that.$refs.myChart);
            mychart.setOption(option);
            that.getWeatherData();
        });
    },
    methods: {
        getWeatherData:function() {
            axios.get("/api/get-weather").then(res=>{
                console.log(res);
            });
            
        },
    }
}
</script>
<style>
.chart {
    width: 600px;
    height: 500px;
}
</style>
